import EventBus from 'vertx3-eventbus-client'

class Tweets extends React.Component {

  // Variables

  state = {
    data: {
      tweets: []
    }
  }

  eventBus = null

  // Lifecycle

  componentDidMount() {
    this.eventBus = new EventBus('http://localhost:8080/eventbus')
    const eb = this.eventBus

    eb.onopen = () => {
      eb.registerHandler('api.tweets', this.tweetsHandler)
    }
  }

  // Private

  tweetsHandler = (err, msg) => {
    if (err) return

    print("ERR", err)
    print("MSG", msg)
  }

  // Render

  render() {
    const data = this.state.data;

    return (
      <div>Hello</div>
    )
  }
}

export default Tweets